// ** React Imports
import { useEffect } from 'react'
import { useParams, Link } from 'react-router-dom'

// ** Store & Actions
import { contestcricketshow } from '../../../store/actions'
import { useSelector, useDispatch } from 'react-redux'
// ** Reactstrap
import { Row, Col, Alert } from 'reactstrap'

// ** User View Components
import Card1 from './Card1'
import Table1 from './Table1'
import Table2 from './Table2'

// ** Styles
import '@styles/react/apps/app-users.scss'

const UserView = () => {
  // ** Vars
  const store = useSelector(state => state.fix),
    dispatch = useDispatch(),
    { id } = useParams()

  // ** Get suer on mount
  useEffect(() => {    
    dispatch(contestcricketshow(parseInt(id)))
  }, [])

console.log(store.cricket_contest)  

console.log(store.cricket_contest)
  return store.selectedUser !== null && store.cricket_contest !== undefined ? (
    <div className='app-user-view'>
      <h3 className='mb-2'>Challenges</h3>
      <Row>
        <Col xl='12' lg='12' md='12'>
          <Card1 contest = {store.cricket_contest} />
        </Col>
      </Row>
      <Row>
        <Col md='12'>
          <Table1                  
          title = {'Contest Prize'} />
        </Col>
      </Row>
      <Row>
        <Col sm='12'>
          <Table2  title = {'Joined Contests'}/>
        </Col>
      </Row>
    </div>
  ) : (
    <Alert color='danger'>
      <h4 className='alert-heading'>User not found</h4>
      <div className='alert-body'>
        User with id: {id}  exist. Check list of all Users: <Link to='/apps/user/list'>Users List</Link>
      </div>
    </Alert>
  )
}
export default UserView
